import React, { Component } from "react";

export default class MeowfficerList extends Component {
  // componentDidUpdate() {
  //   this.props.ReactTooltip.rebuild();
  // }

  render() {
    const catColor    = "cat" + this.props.color;
    const catImage    = this.props.image;
    const catName     = this.props.name;
    const catRarity   = this.props.rarity;
    const catType     = this.props.type;
    const catSlot     = this.props.slot;
    const catTimer    = this.props.timer;
    const catFaction  = this.props.faction;
    const catTactics  = this.props.tactics;
    const catSupport  = this.props.support;
    const catCommand  = this.props.command;

    const openModal   = () => {
      this.props.openModal({
        color   :catColor  ,
        image   :catImage  ,
        name    :catName   ,
        rarity  :catRarity ,
        type    :catType   ,
        slot    :catSlot   ,
        timer   :catTimer  ,
        faction :catFaction,
        tactics :catTactics,
        support :catSupport,
        command :catCommand,
      })
    };
    return (
      <tr onClick={openModal}>
        <th scope="row" className={catColor}>
          <img
            alt="..."
            className="img-fluid"
            referrerPolicy="no-referrer"
            src={catImage}
          />
        </th>
        <td>{catName}</td>
        <td>{catRarity}</td>
        <td>{catType}</td>
        <td>{catSlot}</td>
        <td>{catTimer}</td>
        <td>{catFaction}</td>
        <td>{catTactics}</td>
        <td>{catSupport}</td>
        <td>{catCommand}</td>
      </tr>
    )
  };
};