import React, { useState } from "react";
import {
  Table,
  Modal,
  ModalBody
} from "reactstrap";

import MeowfficerThead from "views/components/MeowfficerThead";
import MeowfficerList from "views/components/MeowfficerList";

import { MeowfficerData } from "assets/data/MeowfficerData";

import MeowfficerCommand from "assets/AL/MeowfficerCommand.png";
import MeowfficerSupport from "assets/AL/MeowfficerSupport.png";
import MeowfficerTactics from "assets/AL/MeowfficerTactics.png";

// core components
import CSNavbar from "components/Navbars/Navbar.js";
import Footer from "components/Footer/Footer.js";

function Meowfficer() {
  React.useEffect(() => {
    document.body.classList.toggle("meowfficer");
    // Specify how to clean up after this effect:
    return function cleanup() {
      document.body.classList.toggle("meowfficer");
    };
  },[]);

  const [modal, setModal]         = useState(false);
  const [modalData, setModalData] = useState({});


  const openModal = (cat) => {
    setModalData({
      color  : cat.color  ,
      image  : cat.image  ,
      name   : cat.name   ,
      rarity : cat.rarity ,
      type   : cat.type   ,
      slot   : cat.slot   ,
      timer  : cat.timer  ,
      faction: cat.faction,
      tactics: cat.tactics,
      support: cat.support,
      command: cat.command,
    });
    setModal(!modal);
  }

  return (
    <>
      <CSNavbar />
      <div className="wrapper">
        <div className="page-header meowfficerheader">
          <div className="content">
            <h1>Meowfficer List</h1>
            <Table>
              <MeowfficerThead />
              <tbody>
                {MeowfficerData.map((cat, key) => {
                  const color   = cat.color;
                  const image   = cat.image;
                  const name    = cat.name;
                  const rarity  = cat.rarity;
                  const type    = cat.type;
                  const slot    = cat.slot;
                  const timer   = cat.timer;
                  const faction = cat.faction;
                  const tactics = cat.tactics;
                  const support = cat.support;
                  const command = cat.command;
                  return (
                  <MeowfficerList
                    key       ={key       }
                    openModal ={openModal }
                    color     ={color     }
                    image     ={image     }
                    name      ={name      }
                    rarity    ={rarity    }
                    type      ={type      }
                    slot      ={slot      }
                    timer     ={timer     }
                    faction   ={faction   }
                    tactics   ={tactics   }
                    support   ={support   }
                    command   ={command   }
                  />
                  );
                })}
              </tbody>
            </Table>
            <Modal isOpen={modal} toggle={openModal}>
              <ModalBody>
                <div className="text-center">
                  <img 
                    className="img-fluid"
                    referrerPolicy="no-referrer"
                    alt="..."
                    src={modalData.image}
                  />
                  <br/>
                  <strong>{modalData.name}</strong>
                </div>
                <Table className="catModalTable">
                  <thead>
                    <tr>
                      <th><img src={MeowfficerCommand} className="img-fluid" referrerPolicy="no-referrer" alt="..." /></th>
                      <th><img src={MeowfficerSupport} className="img-fluid" referrerPolicy="no-referrer" alt="..." /></th>
                      <th><img src={MeowfficerTactics} className="img-fluid" referrerPolicy="no-referrer" alt="..." /></th>
                    </tr>
                  </thead>
                </Table>
              </ModalBody>
            </Modal>
          </div>
        </div>
        <Footer />
      </div>
    </>
  );
}

export default Meowfficer;
